.display {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 17vh;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 85px;
  font-weight: bolder;
  border: 0;
  display: flex;
  justify-content: right;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #868686;
  color: #ffffff;
}
